<template>
    <div>
    </div>
</template>

<script>
export default {
  name: 'wechatPay',
  mounted () {
    this.getWechat()
  },
  methods: {
    onBridgeReady (json) {
      WeixinJSBridge.invoke('getBrandWCPayRequest', json, function (res) {
        // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
        if (res.err_msg === 'get_brand_wcpay_request:ok') {
          this.$toast.center('支付成功')

          // self.location = '#(ctxPath)/success'
        } else {
          this.$toast.center('支付失败')
        }
      })
    },
    getWechat () {

      let postData = {
        orderSn: this.$route.query.orderSn,
        total_fee: this.$route.query.price
      }
      this.$api.product.wechatOrderPay(postData).then(res => {
        if (res.code === 0) {
          var data = JSON.parse(res.data)
          if (typeof WeixinJSBridge === 'undefined') {
            if (document.addEventListener) {
              document.addEventListener('WeixinJSBridgeReady',
                this.onBridgeReady(data), false)
            } else if (document.attachEvent) {
              document.attachEvent('WeixinJSBridgeReady',
                this.onBridgeReady(data))
              document.attachEvent('onWeixinJSBridgeReady',
                this.onBridgeReady(data))
            }
          } else {
            this.onBridgeReady(data)
          }
        } else {
          if (res.code === 2) {
            this.$toast.center(res.message)
          } else {
            this.$toast.center(res.message)
          }
        }
      })
    }
  }
}
</script>

